.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
:root {
  font-size: 16px;
  font-family: 'Open Sans';
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
  --transition-speed: 600ms;
}


.App-header {
  position: relative;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 200;
  color: white;
  
}


.row{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.btn-primary{
  width: auto;
  height: auto;
  font-size: calc(12px + 2vmin);
  text-align: center;
  display: inline-block;
  position: relative;
 overflow: hidden;
  cursor: pointer;
  min-width: 100px;
  color: white;
  background-color: transparent;
  border-radius: 25px;
  padding: 10px;
  border:var(--primary-color) 1px solid;
  transition: all  0.5s ease;
  margin: 5px;
}

.btn-primary:hover span{
  width:400px;
  height: 400px;
}
.btn-primary span{
  display: block;
  border: none;
  transform: translate(-50%,-50%);
  transition: all 0.5s ease;
  position: absolute;
  background: var(--primary-color);
  height: 0px;
  width: 0px;
  border-radius: 50%;
}
.navbar {
  z-index: 15;
  font-weight: 200;
    overflow: hidden;
    position: fixed;
    background-color: var(--bg-primary);
    transition: width 600ms ease;
   
  }
  
  .navbar-nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }
  
  .nav-item {
    width: 100%;
  }
  
  
  
  .nav-link {
    display: flex;
    align-items: center;
    height: 5rem;
    color: var(--text-primary);
    text-decoration: none;
    filter: grayscale(100%) opacity(0.7);
    transition: var(--transition-speed);
  }
  
  .nav-link:hover,.box-item:hover{
    filter: grayscale(0%) opacity(1);
    background: var(--bg-secondary);
    color: var(--text-secondary);
  }
  
  .link-text {
    display: none;
    margin-left: 1rem;
  }
  
  .nav-link svg {
    width: 2rem;
    min-width: 2rem;
    margin: 0 1.5rem;
  }
  .header{
    background-image: linear-gradient(to top, #5ee7df 0%, #b490ca 100%);
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
  }
  .showcase{
position: relative;
width: 100%;
height: 100%;

  }
 #three-d{
  perspective: 100px;
  transform-style: preserve-3d;
  transform: rotateY(31deg) rotateX(351deg) scale(1.5);
    }

  .title-container{
    text-align: start;
    background-color: #282c34;
    width: 50%;
    height: 100%;
    box-sizing: border-box;
  }
  
  .main-title{
    display: block;
    line-height: 1.1;
    font-weight: 600;
    font-size: 50px;
  }
  .side-title{
    font-size: 24px;
  }
  .image-container{
    width: 50%;
    height: 100%;
    box-sizing: border-box;
    border-left: 200px solid  #282c34;
    border-bottom: 100vh solid transparent;
  }
  
  .fa-primary {
    color: #7ef6ff;
    
  }
  
  .fa-secondary {
    color: #49a3df;
  }
  .fa-third{
    color: #df496f;
    
  }
  
  .fa-primary,
  .fa-secondary {
    transition: var(--transition-speed);
  }
  
  .logo {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1rem;
    text-align: center;
    color: var(--text-secondary);
    background: var(--bg-secondary);
    font-size: 1rem;
    letter-spacing: 0.3ch;
    width: 100%;
  }
  
  .logo svg {
    transform: rotate(0deg);
    transition: var(--transition-speed);
  }
  
  .logo-text
  {
    text-align: start;
    left: 10px;
    margin-right: 80px;
    display: inline;
    font-size: 15px;
    position: absolute;
    left: -999px;
    transition: var(--transition-speed);
  }
  
  .navbar:hover .logo svg {
    
    transform: rotate(-180deg);
    font-size: 40px;
  }
  @keyframes slide-in{
    0%{
      
    }
  }
  
  /* Small screens */
  @media only screen and (max-width: 600px) {
    .navbar {
      bottom: 0;
      width: 100vw;
      height: 5rem;
    }
    .home-title{
      font-size: 50px;
    }
    .home-icon{
      width: 300px;
    }
  
    .logo {
      display: none;
    }
  
    .navbar-nav {
      flex-direction: row;
    }
  
    .nav-link {
      justify-content: center;
    }
  
    main {
      margin: 0;
    }
    .info>span{
      justify-content: flex-start;
      padding-bottom:50px;
      
    }
  }
  
  /* Large screens */
  @media only screen and (min-width: 600px) {
    .navbar {
      top: 0;
      width: 5rem;
      height: 100vh;
    }
  
    .navbar:hover {
      width: 16rem;
    }
  
    .navbar:hover .link-text {
      display: inline;
    }
  
    .navbar:hover .logo svg
    {
      margin-left: 11rem;
    }
  
    .navbar:hover .logo-text
    {
      left: 0px;
    }
   .App-header{
    margin-left: 80px;
   }
  }
  